<script>
import scrollReveal from "scrollreveal";
window.addEventListener('unload', function() {
    window.scrollTo(0, 0)
})

export default {
  data() {
    return {
      scrollReveal: scrollReveal(),
    };
  },
  mounted() {
    this.scrollReveal.reveal(".reveal-bottom-v1", {
      duration: 1000,
      origin: "bottom",
      reset: false,
      mobile: true,
      distance: "150px",
      opacity: 0.001,
      easing: "linear",
    });
    this.scrollReveal.reveal(".reveal-bottom-v2", {
      duration: 1200,
      origin: "bottom",
      reset: false,
      mobile: true,
      distance: "300px",
      easing: "linear",
    });
    this.scrollReveal.reveal(".reveal-bottom-v3", {
      duration: 1500,
      origin: "bottom",
      reset: false,
      mobile: true,
      distance: "300px",
      easing: "linear",
    });

  },
}
</script>
