<template>
  <div class="nav" @mouseenter="NavinSwitch" @mouseleave="NavoutSwitch">
    <router-link to="/">
      <div class="logo"></div>
    </router-link>
    <div class="navItem">
      <ul>
        <li
          id="productLi"
          @mouseenter="vueThrottle(this.showList(), 1000)"
          @mouseleave="vueThrottle(this.hideList(), 1000)"
        >
          產品
        </li>
        <li>技術文件</li>
        <li>實際案例</li>
        <li>聯絡我們</li>
      </ul>
    </div>
    <div
      class="productList"
      @mouseenter="vueThrottle(this.showList(), 1000)"
      @mouseleave="
        vueThrottle(this.hideList(), 1000);
        showproductNavGroup1();
        hideproductNavGroup2();
      "
    >
      <div class="productNav">
        <div
          @click="
            showproductNavGroup1();
            hideproductNavGroup2();
          "
        >
          門禁對講
        </div>
        <div
          @click="
            hideproductNavGroup1();
            showproductNavGroup2();
          "
        >
          智慧監控
        </div>
      </div>
      <div class="productNavGroup1">
        <div class="productNavGroup1Item">
          <div @click="hideList">
            <router-link to="/doorphone">
              <img
                @click="hideList"
                :src="require('../assets/media/productNavGroup1-1.png')"
                alt=""
              />
            </router-link>
          </div>
          <p>大門口機</p>
        </div>
        <div class="productNavGroup1Item">
          <div @click="hideList">
            <router-link to="/doorphone2">
              <img
                :src="require('../assets/media/productNavGroup1-2.png')"
                alt=""
              />
            </router-link>
          </div>
          <p>小門口機</p>
        </div>
        <div class="productNavGroup1Item">
          <div @click="hideList">
            <router-link to="/doorphone3">
              <img
                :src="require('../assets/media/productNavGroup1-3.png')"
                alt=""
              />
            </router-link>
          </div>
          <p>室內機</p>
        </div>
        <div class="productNavGroup1Item">
          <div @click="hideList">
            <router-link to="/doorphone4">
              <img
                :src="require('../assets/media/productNavGroup1-4.png')"
                alt=""
              />
            </router-link>
          </div>
          <p>管理機</p>
        </div>
      </div>
      <div class="productNavGroup2">Coming Soon ...</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      waiting: false,
    };
  },
  methods: {
    NavinSwitch() {
      document.querySelector(".logo").classList.add("class", "logoActive");
      document.querySelector(".nav").classList.add("class", "navOnhover");
    },
    NavoutSwitch() {
      document.querySelector(".logo").classList.remove("logoActive");
      document.querySelector(".nav").classList.remove("navOnhover");
    },
    hideList() {
      document
        .querySelector(".productList")
        .classList.remove("productListActive");
      document.getElementById("productLi").classList.remove("navItemActive");
    },
    showList() {
      document
        .querySelector(".productList")
        .classList.add("class", "productListActive");
      document
        .getElementById("productLi")
        .classList.add("class", "navItemActive");
    },

    // activeNavproductLi() {
    //   document
    //     .getElementById("productLi")
    //     .classList.add("class", "navItemActive");
    // },
    // inactiveNavproductLi() {
    //   document.getElementById("productLi").classList.remove("navItemActive");
    // },
    hideproductNavGroup1() {
      document
        .querySelector(".productNavGroup1")
        .classList.add("class", "productNavGroup1Toggle");
    },
    showproductNavGroup1() {
      document
        .querySelector(".productNavGroup1")
        .classList.remove("productNavGroup1Toggle");
    },
    hideproductNavGroup2() {
      document
        .querySelector(".productNavGroup2")
        .classList.remove("productNavGroup2Toggle");
    },
    showproductNavGroup2() {
      document
        .querySelector(".productNavGroup2")
        .classList.add("class", "productNavGroup2Toggle");
    },
    vueThrottle(callback, limit) {
      return function () {
        if (!this.waiting) {
          callback.apply(this, arguments);
          this.waiting = true;
          setTimeout(function () {
            this.waiting = false;
          }, limit);
        }
      };
    },
  },
};
</script>
