import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'
import Doorphone from '../views/doorphone.vue'
import Doorphone2 from '../views/doorphone2.vue'
import Doorphone3 from '../views/doorphone3.vue'
import Doorphone4 from'../views/doorphone4.vue'
import Product2 from '../views/product2.vue'
import ProductX915 from '../views/productX915.vue'
import ProdcutX933 from '../views/productX933.vue'
import ProductR20B from '../views/productR20B.vue'
import ProductE12 from '../views/productE12.vue'
import ProductE16 from '../views/productE16.vue'
import ProductA05S from '../views/productA05S.vue'
import ProdcutC319 from '../views/productC319.vue'
import ProductR49G from '../views/productR49G.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/doorphone',
    name: 'Doorphone',
    component: Doorphone,
  },
  {
    path: '/doorphone/X915',
    name: 'doorphone-X915',
    component: ProductX915,
  },
  {
    path: '/doorphone2',
    name: 'Doorphone2',
    component: Doorphone2,
  },
  {
    path: '/doorphone2/R20B',
    name: 'doorphone2-R20B',
    component: ProductR20B,
  },
    {
    path: '/doorphone2/A05S',
    name: 'doorphone2-A05S',
    component: ProductA05S,
  },

  {
    path: '/doorphone2/E12',
    name: 'doorphone2-E12',
    component: ProductE12,
  },
  {
    path: '/doorphone2/E16',
    name: 'doorphone2-E16',
    component: ProductE16,
  },
  {
    path: '/doorphone3',
    name: 'doorphone3',
    component: Doorphone3,
  },
  {
    path: '/doorphone3/X933',
    name: 'doorphone3-X933',
    component: ProdcutX933,
  },
  {
    path: '/doorphone3/C319',
    name: 'doorphone3-C319',
    component: ProdcutC319,
  },
  {
    path: '/doorphone4',
    name: 'doorphone4',
    component: Doorphone4,
  },
  {
    path: '/doorphone4/R49G',
    name: 'doorphone4-R49G',
    component: ProductR49G,
  },

  {
    path: '/product2',
    name: 'product2',
    component: Product2,
  },
]


const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior () {       //添加该方法
    return { x: 0, y: 0}
  }
})

export default router
